<template>
  <NavBar :runningTests="runningTests" />
  <router-view
    :url="url"
    :openfaasUrl="openfaasUrl"
    :socket="socket"
    @info="showInfo"
  />

  <!-- Button trigger info modal -->
  <div>
    <button
      type="button"
      class="btn btn-primary hidden"
      data-mdb-toggle="modal"
      data-mdb-target="#Info-Modal"
      id="info-modal-button"
      ref="infoModalButton"
    ></button>
  </div>

  <!-- Info Modal -->
  <div
    class="modal fade"
    id="Info-Modal"
    tabindex="-1"
    aria-labelledby="ModalLabel"
    aria-hidden="true"
    ref="infoModal"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body info-message-container">
          <div>
            <label
              id="info-message"
              :class="{
                green: infoColor === 'green',
                red: infoColor === 'red',
              }"
              >{{ infoMessage }}</label
            >
          </div>
          <div v-if="infoError">
            <label id="info-error">{{ infoErrorText }}</label>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar.vue";

export default {
  name: "App",
  components: {
    NavBar,
  },
  data() {
    return {
      openfaasUrl: "None",
      url: "/local",
      runningTests: 0,
      socket: null,
      socketIntv: null,
      infoMessage: "info",
      infoColor: "green",
      infoError: true,
      infoErrorText: "error",
    };
  },
  methods: {
    configureConnections() {
      this.socket = io(process.env.VUE_APP_ROOT_API);
      this.socketIntv = setInterval(() => {
        this.socket.emit("server_stats", { openfaasurl: this.openfaasUrl });
      }, 1000);
      this.socket.on("server_stats", (msg) => {
        const data = JSON.parse(msg.data);
        if (data.success) {
          this.runningTests = data.count;
        } else {
          clearInterval(this.socketIntv);
          this.runningTests = 0;
        }
      });
    },
    showInfo(message, color, error) {
      this.infoMessage = message;
      this.infoColor = color;
      if (error != null && error != "None") {
        this.infoError = true;
        this.infoErrorText = error;
      } else {
        this.infoError = false;
      }
      if (!$(this.$refs.infoModal).hasClass("show"))
        $(this.$refs.infoModalButton).click();
    },
  },
  // updated(){
  //   console.log(this.socket._callbacks)
  // },
  mounted() {
    //mdb init
    document.querySelectorAll(".form-outline").forEach((formOutline) => {
      new mdb.Input(formOutline).init();
    });
    this.configureConnections();
  },
};
</script>

